.gallery {
  box-sizing: border-box;
  width: 100%;
  height: max-content;
  padding: 20px 30px 20px 20px;
  background-color: #fff;
  border: 2px solid rgba(0, 128, 107, 0.788);
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  justify-items: center;
  align-items: center;
}